.App {
  text-align: center;
  color: #fff;
}

a {
  color: #fff;
  text-decoration: none;
}

.background {
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  position: fixed;
  will-change: transform;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(67, 85, 85)), color-stop(85%, rgb(6, 8, 8)));
  background-image: -webkit-linear-gradient(rgb(67, 85, 85), rgb(6, 8, 8) 85%);
  background-image: -o-linear-gradient(rgb(67, 85, 85), rgb(6, 8, 8) 85%);
  background-image: linear-gradient(rgb(67, 85, 85), rgb(6, 8, 8) 85%);
  z-index: -1;
}

.container {
  margin: 0 auto;
  padding: 0;
  max-width: 1100px;
}

.navbar {
  height: 80px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
  padding: 0 30px;
  z-index: 2;
  font-weight: bold;
  font-size: 25px;
  background-color: rgba(0, 0, 0, 0.2);
}

.libraryLink {
  margin-left: auto;
}


