.player-bar {
  bottom: 0;
  height: 100px;
  position: fixed;
  width: 100%;
}

#buttons {
  width: 300px;
  margin: auto;
  text-align: center;
}

#buttons button {
  border: none;
  background: none;
  font-size: 1.2rem;
  color: hsla(0, 0%, 100%, 0.5);
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
  cursor: pointer;
}

#buttons #play-pause {
  color: hsla(0, 0%, 100%, .8);
  font-size: 2rem;
  margin: 0.5rem 1rem 0;
  -webkit-transform: translateY(0.3rem);
      -ms-transform: translateY(0.3rem);
          transform: translateY(0.3rem);
}

#buttons button:hover {
  color: hsla(0, 0%, 100%, 1);
}

#buttons #play-pause:hover {
  color: hsla(0, 0%, 100%, 1);
}

#time-control {
  margin: 0.5rem auto;
  max-width: 640px;
  width: 50%;
}

#time-control div, #volume-control div {
  font-size: 0.8rem;
  color: hsla(0, 0%, 100%, 0.4);
  width: 35px;
  display: block;
  float: left;
  text-align: center;
}

#time-control input {
  float: left;
  width: calc(100% - 80px);
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}

#volume-control {
  position: absolute;
  right: 2rem;
  width: 15%;
  -webkit-transform: translateY(-30%);
      -ms-transform: translateY(-30%);
          transform: translateY(-30%);
  top: 66%
}

#volume-control div {
  font-size: 0.9rem;
  width: 20px;
}

#volume-control input {
  float: left;
  width: calc(100% - 50px);
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}

input[type=range] {
  margin-top: 7px;
  background: none;
  color: rgb(196, 196, 196);
  display: inline-block;
}


